.header{
    background: linear-gradient(90deg , rgba(180, 164, 143, 0.9) 10%, rgba(40, 44, 52, 0.9) 40%);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 3px 0 5px cadetblue;
    padding: 10px;
    margin-bottom: 20px;
}
.nav__menu{
    display: flex;
    list-style: none;
    gap: 20px;
}
.item{
    background-color: #c5ad90;
    padding: 16px 20px;
    border-radius: 5px;
    box-shadow: 0 0 3px 2px cadetblue;
    transition: 0.3s;
    display: flex;
    align-items: center;
}
.item:hover{
    background-color: rgba(0, 0, 0, 0.52);
    box-shadow: 0 0 3px 2px antiquewhite;
    color: white;
    cursor: pointer;
    transform: translateY(-5px);
}
.item:hover .ico{
    transform: rotate(360deg) scale(1.2);
}
.logo_imagen{
    width: 150px;
}
a{
    text-decoration: none;
    color: #1a1c21;
    font-weight: 500;
    display: flex;
}
.ico{
    font-size: 32px;
    margin: 0 10px 0 0;
    transition: 0.3s;
}