.tabla{
    width: 90%;
    margin: 0 auto;
    background-color: darkgray;
    border: 1px solid black;
    border-radius: 5px;
}
.tabla__thead{
    background-color: #282c34;
    color: darkgray;
}
.tabla__tcaption{
   background-color: #282c34;
    border-radius: 5px;
    height: 60px;
    color: white;
    font-size: 36px;
    margin: 0 auto;
    line-height: 60px;
}
.tabla__td{
    background-color: #1a1c21;
    color: white;
    height: 50px;
    text-align: center;
}
